.logo {
  color: rgb(19, 11, 11);
  font-size: 60px;
}
.pages ul li {
  font-size: 30px;
  justify-content: right;
}
nav {
  background-color: #499ed0;
  padding: 10px 15px;
  display: flex;
  justify-content: space-between;
  width: 25%-75%;
  border-radius: 70px;
  border: 4px solid black;
}

ul {
  list-style-type: none;
  margin: 15px;
  padding: 10px;
}

li {
  display: inline;
  margin-right: 20px;
}

li:last-child {
  margin-right: 0;
}

a {
  color: #0d0c0c;
  text-decoration: none;
  font-size: 60px;
}

a:hover {
  color: #e3ea7e;
}

/* Page Content */
.container {
  max-width: 1600px;
  max-height: 800px;
  margin: 80px bold;
  padding: 5px 20px;
}

h2 {
  color: #bc82f3;
  font-size: 70px;
  text-align: left;
}

p {
  color: #8238c6;
  font-size: 70px;
}

.home h2 {
  color: blueviolet;
}

.contact h2 {
  color: blueviolet;
}

.about h2 {
  color: blueviolet;
}

.name {
  color: rgb(5, 72, 126);
  font-size: 40px;
}
